import Env from './env'

/*
* 
*/
export default function install(Vue){
  // 
  document.title = Env.title;
  // 
  Vue.prototype.$config = Env;
  // 
  Vue.prototype.$obj = {
    scan: null,
    setTime: null,
    socket: null,
  };
  // 
  Vue.prototype.$reg = (name,val)=>{
    let isRight=false;
    let msg='';
    const reg = {
      uname: /^[a-zA-Z][a-zA-Z0-9\_\@\-\*\&]{4,15}$/,
      tel: /^[1]\d{10}$/,
      email: /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/,
      vcode: /^\d{4}$/,
      passwd: /^[a-zA-Z0-9|_|@|-|*|&]{6,16}$/,
    }
    switch(name){
      case 'uname':
        isRight = reg.uname.test(val);
        msg = !isRight?'5~16':'';
        break;
      case 'tel':
        isRight = reg.tel.test(val);
        msg = !isRight?'':'';
        break;
      case 'email':
        isRight = reg.email.test(val);
        msg = !isRight?'':'';
        break;
      case 'vcode':
        isRight = reg.vcode.test(val);
        msg = !isRight?'4':'';
        break;
      case 'passwd':
        isRight = reg.passwd.test(val);
        msg = !isRight?'6~16':'';
        break;
    }
    return isRight?true:msg;
  }

}